import React,{useState} from "react";
import { BrowserRouter as Router, Route } from 'react-router-dom';

import ListPlace from '../list-place';
import List from "../list-event";
import CartEvent from '../cart-event';
import Header from '../header';
import SelectCity from '../select-city';
import './app.css';

const App = () => {
  
  const  [ citySlug, setCitySlug] = useState('');

 const getSitySlug = (city)=>{
  setCitySlug(city)
  }

  return (
      <Router>
          <div  className='app'>
            <Header />
          <div className='container'>   
              <Route  path='/events/' exact render={()=>  <List slug={citySlug}/> } />  
              <Route  path='/place/' exact render={()=>  <ListPlace slug={citySlug}/> } />  
              <Route  path='/' exact   render={()=>  <SelectCity getSitySlug={getSitySlug}/> }/>  

              <Route  path='/events/:id' render={({match})=>  <CartEvent selectId={match.params.id}/> }/>  
                   
            </div> 
        
      </div>
     </Router>
  );
};

export default App;